import React from 'react';
import logo from './favicon.png'
import './stylesheets/stylesheet.scss';
import {Component} from 'react';

var data = {
  hello: 5,
  hi: 7
}
/* a(href="" + base_url + "")
        span.inner
          span.img
            img(src="" + base_url + "images/favicon.png")
          span Redcoat
          */

class Navbar extends Component {
  render() {
    return (
      <nav id="navbar">
        <div className="navbar-left">
          <div id="logo">
            <a href="/redcoat">
              <span className="inner">
                <span className="img">
                  <img src={logo}/>
                </span>
                <span>Redcoat</span>
              </span>
            </a>
          </div>
        </div>
        <div className="navbar-centre">Tagging Interface (WIP)</div>
        <div className="navbar-right">
          <div className="dropdown-menu short">
            <a href="features">v1.0</a>
          </div>
        </div>
      </nav>
    )
  }
}



class Word extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <span className="word">
        <span className="word-inner">
          {this.props.text}
        </span>
      </span>
    );
  }
}


class Sentence extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="sentence">
        { this.props.words.map(word => <Word text={word} />)}
      </div>
    );
  }

}


class MainWindow extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: {documentGroup: []}
    }
  }

  componentWillMount() {

    const fetchConfig = {
        method: 'GET',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        }
      };

    fetch('http://localhost:3000/projects/zvJohtRLH/tagging/getDocumentGroup', fetchConfig) // TODO: move localhost out
      .then(response =>
        response.text())
      .then((data) => {         
        this.setState({data: JSON.parse(data)}, () => { console.log(this.state.data) })
      });
  }

  render() {

    console.log(this.state.data.documentGroup)

    return (
      <div id="tagging-container">
        <div id="sentence-tagging">
          { this.state.data.documentGroup.map(doc => <Sentence words={doc}/>)}
        </div>



      </div>

    )

  }
}

function App() {
  return (
    <div id="app">
      <Navbar/>  
      <div class="tagging"></div>
      <div id="main-container">      
        
        <main id="main">  
          <MainWindow data={data}/> 
        </main>        
      </div>
      <div id="tagging-menu">
          <div className="category-hierarchy">
            <div className="tokens-info">Hello I am wikipedia</div>
            <div id="modify-hierarchy-container">Modify hierarchy container</div>
            <div id="category-hierarchy-tree">Tree</div>


          </div>
        </div>
    </div>
  );
}


export default App;
